.list-item {
  display: flex;
  align-items: center;
}

.box {
  background: white;
  border-radius: 6px;
  box-shadow: -10px 10px 40px rgba(0, 0, 0, 0.03);
  margin-bottom: 20px;
}

.box.parallax-window {
  background-color: transparent;
}

.box__body {
  padding: 30px;
}

@media (max-width: 767px) {
  .box {
    margin-left: -15px;
    margin-right: -15px;
    border-radius: 0;
    margin-bottom: 10px;
  }
}
